<template>
    <div>
        <div class="title">热销推荐</div>
        <ul>
            <router-link :to="'/detail/'+item.id" tag="li" class="item border-bottom" v-for="item of list" :key="item.id">
                <img class="item-img" :src="item.recommendUrl">
                <div class="item-info">
                    <p class="item-title">{{item.title}}</p>
                    <p class="item-text">{{item.text}}</p>
                    <button class="item-btn">查看详情</button>
                </div>
            </router-link>
        </ul>
    </div>
</template>

<script>
export default {
    name: "HomeRecommend",
    props:{list:Array},
}
</script>

<style lang="stylus" scoped>
@import "~styles/mixins.styl";
.title
    margin-top:.2rem
    background: #eee
    line-height: .8rem
    text-indent: .2rem
.item
    display: flex
    overflow: hidden
    height:1.9rem
    .item-img
        width:1.7rem
        height 1.7rem
        padding: .1rem
    .item-info
        felx: 1
        padding: .1rem
        min-width: 0
        .item-title
            line-height:.54rem
            font-size:.32rem
            ellipsis()
        .item-text
            line-height:.4rem
            color:#ccc
            ellipsis()
        .item-btn
            line-height:.44rem
            background: #ff9300
            padding: 0 .2rem
            border-radius: .06rem
            margin-top: .16rem
            color: #fff
</style>



