<template>
    <div>
        <div class="title">周末去哪儿</div>
        <ul>
            <li class="item border-bottom" v-for="item of list" :key="item.id">
                <div class="item-img-wrapper">
                <img class="item-img" :src="item.weekendUrl">
                </div>
                <div class="item-info">
                    <p class="item-title">{{item.title}}</p>
                    <p class="item-text">{{item.text}}</p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "HomeWeekend",
    props:{list:Array}
}
</script>

<style lang="stylus" scoped>
@import "~styles/mixins.styl";
.title
    background: #eee
    line-height: .8rem
    text-indent: .2rem
.item-img-warpper
    width:100%
   
.item-img
    width:100%
    padding: .1rem
    box-sizing:border-box
.item-info
    padding: .1rem
    min-width: 0
    .item-title
        line-height:.54rem
        font-size:.32rem
        ellipsis()
    .item-text
        line-height:.4rem
        color:#ccc
        ellipsis()
</style>



