<template>
<div class="header">
    <div class="header-left">
        <div class="iconfont back-icon">&#xe658;</div>
    </div>
    <div class="header-input "><span class = "iconfont">&#xe63c;</span>输入城市/景点/游玩主题</div>
    <router-link to="/city">
    <div class="header-right">{{this.city}}<span class="iconfont arrow-icon">&#xe65c;</span></div>
    </router-link>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default{
    name: 'homeHeader',
    computed:{
        ...mapState(['city'])
    }
}

</script>
<style lang="stylus" scoped>
@import "~styles/varibles.styl"
@import '~styles/mixins.styl';
.header
    display: flex
    line-height: .86rem
    background: $bgColor
    color :#fff
    .header-left
        width:.64rem
        float: left
        .back-icon
            text-align:center
            font-size:.6rem
    .header-input
        flex:1
        height: .64rem
        line-height: .64rem
        margin-top: .12rem
        margin-left: .2rem
        padding-left: .2rem
        background :#fff
        border-radius :.1rem
        color: #ccc
        text-align:center
    .header-right
        color:#fff
        min-width:1.24rem
        padding:0 .1rem
        float: right 
        text-align:center
        ellipsis()
        .arrow-icon
            font-size 0.4rem
</style>





